.nav_container{
    height: 4rem;
    top: 0;
    width: 100vw;
    background-color: #000;
    color: #fff;
    position: fixed;
}
.nav_container .cart_icon{
    font-size: 2rem;
    float: right;
    margin: 10px 20px 0 0;
    cursor: pointer;
}
.profile_options{
    position: absolute;
    right: 7%;
    top: 7%;
    display: grid;
    border-radius: 20px 0px 20px 20px;
    background-color: #fff;
    color: #000;
    border: 1px solid gray;
}
.profile_options span{
    padding: 10px 15px;
}
.cart__counter{
    font-size: 17px;
    padding: 2px 5px 0 5px;
    border-radius: 50%;
    position: fixed;
    right: 21px;
    top: 2px;   
    animation: counter 0.7s infinite;
}

@keyframes counter {
    25%{top: 0.5%;}
    25%{top: 1%;}
    25%{top: 1.5%;}
    25%{top: 2%;}
}