 .cart_container{
    position: absolute;
    top: -14%;
    right: 0;
    height: 100vh;
    width: 30vw;
    padding: 1rem;
    background-color: #fff;
    box-shadow: -11px 6px 26px -7px rgba(0,0,0,0.84);
    -webkit-box-shadow: -11px 6px 26px -7px rgba(0,0,0,0.84);
    -moz-box-shadow: -11px 6px 26px -7px rgba(0,0,0,0.84);
    animation: cart 1s;
    }
.cart_cross{
    animation: cross 1s;
}
@keyframes cross {
    from {right: 0;}
    to {right: -30vw;}
}

@keyframes cart {
    from {right: -30vw;}
    to {right: 0;}
}

.cart_container .icon{
    float: right;
    cursor: pointer;
}

.cart_container .cart_header{
    border-bottom: 1px solid #000;
}
.cart_items{
    display: flex;
    border: 1px solid lightgray;
    padding: 5px;
    margin: 5px 10px 0 0;
}
.cart_items_content{
    padding: 0 10px;
}
